* {
    box-sizing: border-box;
  }
  
  .flex-container {
    display: flex;
    flex-wrap: wrap;
    font-size: 20px;
    padding: 10px;
    text-align: center;
    align-items: center;
  }
  .flex-containercard {
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
    text-align: center;
  }
  
  .flex-item-left {
    padding: 10px;
    flex: 50%;
  }
  
  .flex-item-right {
    padding: 10px;
    flex: 50%;
  }
  .flex-item-cardleft {
    padding: 10px;
    flex: 50%;
    text-align: justify;
  }
  
  .flex-item-cardright {
    padding: 10px;
    flex: 50%;
    text-align: justify;
  }
  .textbox
  {
    text-align: justify;
  }
  .resume
  {
    text-align: center;
  }
  
  @media (max-width: 768px) {
    *
    {
      font-size: 15px;
    }
    .flex-item-right, .flex-item-left {
      flex: 100%;
      padding: 5px;
    }
    .flex-item-cardright, .flex-item-cardleft {
      flex: 100%;
      padding: 5px;
    }
  }